<template>
  <div class="hb-alert-tip" :style="style">
    <div class="center">
      <div class="hb-close" @click="closeTip">
        <img src="../../../assets/images/games/collection/common/hb-alert-close.png" alt="">
      </div>
      <slot name="advertising"></slot>
    </div>
  </div>
</template>

<script>
import { getZIndex } from '../../../utils/zindex'
export default {
  name: 'hongbaoTip',
  data () {
    return {
      zIndex: getZIndex() + 2
    }
  },
  props: {
  },
  methods: {
    closeTip () {
      this.$emit('closeTip')
    }
  },
  computed: {
    style () {
      return {
        'z-index': this.zIndex
      }
    }
  }
}
</script>

<style lang="less" scoped>
.hb-alert-tip{
  width: 6.8rem;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  margin: -3rem 0 0 0;
  .center{
    .hb-close{
      margin-left: 6.1rem;
      margin-bottom: .28rem;
      width: .7rem;
      height: .7rem;
      img{
        width: 100%;
        display: block;
      }
    }
  }
}
</style>



