<template>
<div>
  <div class="cbt-top">
    <img :src="imgSrc[0]">
  </div>
  <div class="treasure-map">
    <img :src="imgSrc[1]">
  </div>
  <a href="">
    <div class="treasure">
      <img :src="imgSrc[2]">
    </div>
    <div class="tag1">
      <img :src="imgSrc[3]">
    </div>
  </a>
  <a href="">
    <div class="boat">
      <img :src="imgSrc[4]">
    </div>
    <div class="tag2">
      <img :src="imgSrc[5]">
    </div>
  </a>
  <a href="">
    <div class="castle">
      <img :src="imgSrc[6]">
    </div>
    <div class="tag3">
      <img :src="imgSrc[7]">
    </div>
  </a>
  <a href="/nhshb.html">
    <div class="coconut_tree1"></div>
    <div class="tag4">
      <img :src="imgSrc[8]">
    </div>
  </a>
  <a href="javascript:;">
    <div class="pyramid">
      <img :src="imgSrc[9]">
    </div>
    <div class="tag5">
      <img :src="imgSrc[10]">
    </div>
  </a>
  <hongbao-tip v-if="isShowHbTip" @closeTip="closeTip">
    <div slot="advertising">
      <a href="/common.html" class="adLink">
            <img src="https://yuanshanbao.oss-cn-beijing.aliyuncs.com/test_file/ms/1521896128059_6317.png" alt="">
          </a>
    </div>
  </hongbao-tip>
  <overlay v-if="isShowHbTip"></overlay>
</div>
</template>

<script>
import modal from '@/components/common/modal'
import overlay from '@/components/common/overlay'
import loading from '@/components/common/loading'
import hongbaoTip from '@/components/collection/common/hongbaoTip'
import ASIGN from '@/config/const'

export default {
  name: 'App',
  data() {
    return {
      isShowOverlay: false,
      isShowLoading: false,
      isShowHbTip: true,
      imgSrc: [
        ASIGN.cdnUrl + '/collection/cbt/cbt_top.png',
        ASIGN.cdnUrl + '/collection/cbt/land.png',
        ASIGN.cdnUrl + '/collection/cbt/treasure.png',
        ASIGN.cdnUrl + '/collection/cbt/tag_lhf.png',
        ASIGN.cdnUrl + '/collection/cbt/boat.png',
        ASIGN.cdnUrl + '/collection/cbt/tag_qvip.png',
        ASIGN.cdnUrl + '/collection/cbt/castle.png',
        ASIGN.cdnUrl + '/collection/cbt/tag_tlb.png',
        ASIGN.cdnUrl + '/collection/cbt/tag_ysj.png',
        ASIGN.cdnUrl + '/collection/cbt/pyramid.png',
        ASIGN.cdnUrl + '/collection/cbt/tag_jjdl.png'
      ]
    }
  },
  components: {
    modal,
    overlay,
    loading,
    hongbaoTip
  },
  methods: {
    closeTip() {
      this.isShowHbTip = false
    }
  },
  created() {}
}
</script>

<style lang='less'>
@baseUrl: '../../../../assets/styles/games';

@import '@{baseUrl}/common/base.less';
@import '@{baseUrl}/collection/common/common.less';
@import '@{baseUrl}/collection/cbt/cbt.less';
</style>



